* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  height: 100vh;
  padding: 0 10px;
  background: rgb(238, 174, 202);
  background: radial-gradient(
    circle,
    rgba(238, 174, 202, 1) 0%,
    rgba(148, 187, 233, 1) 100%
  );
}

.header-sec {
  text-align: center;
  margin-top: 100px;
}

.header-sec h1 {
  font-size: 40px;
  color: #fff;
}

.get-input {
  width: 100%;
  max-width: 500px;
  margin: 0px auto;
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.get-input input {
  height: 30px;
  width: 95%;
  padding: 2px;
  outline: none;
  border: none;
}

.get-input button {
  padding: 5px 8px;
  background-color: rgba(148, 187, 233, 1);
  border: none;
  color: #fff;
  outline: none;
  cursor: pointer;
}

.show-results {
  width: 100%;
  max-width: 500px;
  margin: 0px auto;
  margin-top: 20px;
}

.show-results tr {
  background-color: rgb(90, 130, 175);
}

.show-results td {
  padding: 10px;
  color: #fff;
}

.show-results .show-input {
  font-weight: bold;
  width: 60%;
}

.show-results .quantity {
  text-align: center;
  width: 20%;
}

.show-results .quantity i:first-child {
  margin-right: 8px;
  cursor: pointer;
}

.show-results .quantity i:last-child {
  margin-left: 8px;
  cursor: pointer;
}

.show-results .quantity span {
  font-size: 22px;
}

.show-results .checkout {
  text-align: center;
  background-color: green;
  cursor: pointer;
  width: 10%;
}

.completed {
  text-decoration: line-through;
  opacity: 0.5;
}

.show-results .trash {
  background-color: red;
  text-align: center;
  cursor: pointer;
  width: 10%;
}

.total-items {
  width: 100%;
  max-width: 500px;
  margin: 5px auto;
  text-align: right;
}

.total-items p {
  font-size: 22px;
  color: #fff;
  font-weight: bold;
}
@media (max-width: 400px) {
  .show-results .show-input {
    width: 50%;
  }

  .show-results .quantity {
    width: auto;
  }
  .show-results .checkout {
    width: auto;
  }
  .show-results .trash {
    width: auto;
  }
}
